<template lang="pug">
.curso-main-container.pb-3
  BannerInterno
  .container.tarjeta.tarjeta--blanca.p-4.p-md-5.mb-5
    #t_2_3
    .titulo-segundo.color-acento-contenido(data-aos="fade-right")
      h2 2.3.	Técnicas de manejo y conducción de grupos

    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p El manejo y conducción de grupos turísticos constituye una de las competencias más importantes del guía o intérprete del patrimonio. No se trata solo de acompañar físicamente a los visitantes, sino de liderar una experiencia colectiva donde el orden, la comunicación y la seguridad son esenciales. De acuerdo con la Organización Mundial del Turismo (OMT, 2014), “el guía turístico desempeña un papel fundamental como mediador entre el entorno visitado y los visitantes, siendo responsable de la interpretación, orientación y seguridad del grupo”. Por ello, el dominio de técnicas adecuadas permite mantener la cohesión del grupo y garantizar una experiencia satisfactoria.
      p Entre las técnicas básicas de conducción de grupos se encuentran la planificación previa del recorrido, la presentación inicial, la organización espacial del grupo, el uso de señales y lenguaje corporal, la comunicación asertiva y la escucha activa, el manejo de conflictos y la evaluación continua del grupo. A continuación, se detallan aspectos importantes de estas técnicas:

    .row.justify-content-center(data-aos="fade-right")
      .col-md-9.mb-5.mb-md-0
        LineaTiempoD
          .row(numero="1" titulo="Conocer el grupo")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/tema4/rec17-img1.svg', alt='Texto que describa la imagen')
                
            .col-md-9.mb-4.mb-md-0
              br
              p Antes de iniciar la actividad, el guía debe conocer el tamaño del grupo, la edad promedio, el estado físico y los intereses de los participantes. Esto permite definir estrategias de desplazamiento (por filas, parejas, grupos pequeños, etc.), pausas de descanso y puntos de encuentro.

          .row(numero="2" titulo="Presentación del guía")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/tema4/rec17-img2.svg', alt='Texto que describa la imagen')
                
            .col-md-9.mb-4.mb-md-0
              br
              p La presentación inicial, además, cumple una función motivadora y de liderazgo, ya que genera confianza, empatía y credibilidad entre el guía y los visitantes.

          .row(numero="3" titulo="Uso de señales y lenguaje corporal")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/tema4/rec17-img3.svg', alt='Texto que describa la imagen')
            
            .col-md-9.mb-4.mb-md-0
              br
              p Un guía que utiliza gestos claros, contacto visual y posturas abiertas proyecta seguridad y autoridad sin necesidad de elevar la voz. En entornos naturales o urbanos con ruido, las señales manuales estandarizadas (como levantar el brazo para reunir al grupo o usar un silbato de atención) son recursos eficaces.

          .row(numero="4" titulo="Liderazgo visual")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/tema4/rec17-img4.svg', alt='Texto que describa la imagen')
            
            .col-md-9.mb-4.mb-md-0
              br
              p “El liderazgo visual del guía contribuye al orden, la seguridad y la percepción positiva del servicio”, lo que refuerza la comunicación no verbal como herramienta esencial en la conducción turística. López y González (2020). 
          
          .row(numero="5" titulo="Comunicación asertiva y escucha activa")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/tema4/rec17-img5.svg', alt='Texto que describa la imagen')
            
            .col-md-9.mb-4.mb-md-0
              br
              br
              p Escuchar las inquietudes, sugerencias o molestias del grupo permite anticiparse a conflictos y mejorar la satisfacción general. La conducción exitosa se basa en una relación de respeto mutuo: el guía orienta, pero también adapta su discurso y ritmo según la dinámica del grupo.
          
          .row(numero="6" titulo="Adaptación al público")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/tema4/rec17-img6.svg', alt='Texto que describa la imagen')
            
            .col-md-9.mb-4.mb-md-0
              br
              br
              p En recorridos con familias o adultos mayores, por ejemplo, es importante hablar pausadamente y ofrecer descansos frecuentes; mientras que en grupos jóvenes o de aventura se recomienda mantener un tono enérgico y motivador.
          
          .row(numero="7" titulo="Manejo de conflictos")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/tema4/rec17-img7.svg', alt='Texto que describa la imagen')
            
            .col-md-9.mb-4.mb-md-0
              p Pueden surgir desacuerdos por diferencias culturales, incumplimiento de horarios o expectativas no cumplidas. El guía debe intervenir con diplomacia, escuchar ambas partes y recordar las normas acordadas al inicio del recorrido. Un ejemplo es una caminata ecológica donde algunos avanzan más rápido: el guía puede dividir el grupo y asignar un co-guía al subgrupo ágil, manteniendo comunicación constante.
          
          .row(numero="8" titulo="Evaluación continua")
            .col-md-3
              figure
                img(src='@/assets/curso/temas/tema2/tema4/rec17-img8.svg', alt='Texto que describa la imagen')
            
            .col-md-9.mb-4.mb-md-0
              br
              p Por último, la evaluación continua del grupo durante el recorrido es una técnica clave. Observar las expresiones, el nivel de cansancio o el interés del grupo permite realizar ajustes en tiempo real: acortar una parada, ampliar una explicación o modificar el orden de las actividades.


    br
    .row.justify-content-center.align-items-center.mb-4(data-aos="fade-left")
      p Como señala Álvarez (2019), “el guía eficaz es aquel que lee el lenguaje emocional del grupo y adapta su conducción sin perder el control operativo”. Esta flexibilidad distingue a los guías experimentados y fortalece la calidad del servicio turístico.
      
